@import url(https://fonts.googleapis.com/css?family=Kreon|Quicksand);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #c0c0c0;
  background-image: url(/BreweryFinder/static/media/Beer.30fc55af.jpg);
  opacity: 0.9;

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Quicksand-Text {
  font-family: "Quicksand", sans-serif !important;
  text-align: top;
  text-transform: uppercase;
  text-indent: 50px;
  text-shadow: -1px -1px 0px rgba(255, 255, 255, 2),
    1px 1px 0px rgba(0, 0, 0, 2);
  padding-top: 100px;
  font-size: 50px;
}

.typing-text {
  text-shadow: -1px -1px 0px rgba(255, 255, 255, 2),
    1px 1px 0px rgba(0, 0, 0, 0.9);
  color: #00b6ff;
  opacity: 0.9;
  font-size: 2.2em;
  font-family: "Quicksand", sans-serif !important;
}

.radio-button {
  text-shadow: -1px -1px 0px rgba(0, 0, 0, 6),
    1px 1px 0px rgba(255, 255, 255, 6);
  font-size: 18px;
  -webkit-backdrop-filter: contrast(80%);
          backdrop-filter: contrast(80%);
  padding-left: 400px;
}

.Kreon-Text {
  font-family: "Kreon", serif !important;
}

.Glow {
  color: #61dafb;
  font-size: 4.5em;
  color: #00b6ff;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
}

@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 0.05em #ffddad, 0 0 0.1em #ffddad, 0 0 0.15em #7cd9ff,
      0 0 0.35em #7cd9ff, 0 0 0.4em #7cd9ff, 0 0 0.55em #7cd9ff,
      0 0 0.7em #7cd9ff;
  }
  to {
    text-shadow: 0 0 0.15em #fff, 0 0 0.25em #ffddad, 0 0 0.35em #5bd0ff,
      0 0 0.55em #5bd0ff, 0 0 0.65em #5bd0ff, 0 0 0.75em #5bd0ff,
      0 0 0.85em #5bd0ff;
  }
}

@keyframes glow {
  from {
    text-shadow: 0 0 0.05em #ffddad, 0 0 0.1em #ffddad, 0 0 0.15em #7cd9ff,
      0 0 0.35em #7cd9ff, 0 0 0.4em #7cd9ff, 0 0 0.55em #7cd9ff,
      0 0 0.7em #7cd9ff;
  }
  to {
    text-shadow: 0 0 0.15em #fff, 0 0 0.25em #ffddad, 0 0 0.35em #5bd0ff,
      0 0 0.55em #5bd0ff, 0 0 0.65em #5bd0ff, 0 0 0.75em #5bd0ff,
      0 0 0.85em #5bd0ff;
  }
}

.navbar {
  margin-bottom: 10px;
  background-color: black;
}

